@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;

  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet() {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop() {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onDesktopHigh() {
  @media (min-width: 2560px) {
    @content;
  }
}

